"use client";

// React
import React from "react";

// MUI
import {
  Container,
  Grid,
  Box,
  Stack,
  Divider,
  Typography,
} from "@mui/material";

// Styles
import "./TopBar.module.css";

const TopBar = () => {
  return (
    <>
      <Container
        component="section"
        maxWidth="lg"
        sx={{ backgroundColor: "var(--main-color)", paddingBlock: "10px" }} //  Custom styles
      >
        <Grid container component="div">
          <Grid item>
            <Stack direction="row" alignItems="center" spacing={1}>
              <Typography variant="body2" color="white">
                Free Shipping for orders over $100
              </Typography>
            </Stack>
          </Grid>
          <Grid item>
            <Stack direction="row" alignItems="center" spacing={1}>
              <Typography variant="body2" color="white">
                Call Us: +1 234 567 890
              </Typography>
            </Stack>
          </Grid>
        </Grid>
      </Container>
    </>
  );
};

export default TopBar;
